import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './Header';
import routes from './router.config';
import './App.css'


function RouteWithSubRoutes(route) {
	if (route.pri) {
		console.log(route);
		return <PrivateRoute
			path={route.path}
			component={route.component}
			routes={route.routes}
		/>
	}
	return (
		<Route
			path={route.path}
			render={props => (
				// pass the sub-routes down to keep nesting
				<route.component {...props} routes={route.routes} />
			)}
		/>
	);
}

//权限验证
function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{...rest}
			render={({ location }) =>
				localStorage.getItem('userinfo') ? (
					<Com {...rest} />
				) : (
						<Redirect
							to={{
								pathname: "/login",
								state: location 
							}}
						/>
					)
			}
		/>
	);
}

const App = () => {
	return <Router>
		<Header />
		<Switch>
			{routes.map((route, i) => (
				<RouteWithSubRoutes key={i} {...route} />
			))}
			{/* home组件这种配置方式会在home组件的props内注入四个属性
			<Route exact path='/' component={Home} />
			<PrivateRoute path='/doc' component={Doc} />
			<PrivateRoute path='/tuto' component={Tuto}/>
			<PrivateRoute path='/blog' component={Blog}/>
			<Route path='/comm' component={Comm}/>
            <Route path='/login' component={Login}/>
			<Route path='*' component={()=><div>404</div>}/>
			{
				routes.map(({pri,...rest})=>{
					if(pri){
						return <PrivateRoute {...rest}/>
					}
					return <Route {...rest}/>
					//两个效果一样
					// <Route path={route.path} component={route.component}/>
					// <Route {...route}/>
				})
			} */}
		</Switch>
	</Router>
}


export { RouteWithSubRoutes, App };